<template>
  <div class="nav-menu">
    <div class="nav-icon">
      <span v-if="!$store.state.collapse">ACM打卡签到</span>
    </div>
    <el-menu
      :collapse="$store.state.collapse"
      :default-active="$route.path"
      active-text-color="#6f6"
      background-color="#282c34"
      text-color="#fff"
    >
      <el-menu-item
        v-for="(item, key) in menuItemsInfo"
        :index="item.path"
        :key="key"
        @click="handleItem(item.path)"
      >
        <i :class="item.icon"></i>
        <template #title>{{ item.title }}</template>
      </el-menu-item>
    </el-menu>
  </div>
</template>

<script lang="ts">
import { defineComponent } from 'vue'
import { useRouter } from 'vue-router'

export default defineComponent({
  name: 'NavMenu',
  setup() {
    const router = useRouter()
    const menuItemsInfo = [
      {
        path: '/user-info',
        icon: 'el-icon-chat-line-square',
        title: '个人信息'
      },
      {
        path: '/user-train-info',
        icon: 'el-icon-reading',
        title: '个人训练记录'
      }
    ]
    const adminMenus = [
      {
        path: '/team-train-info',
        icon: 'el-icon-stopwatch',
        title: '全队训练信息'
      },
      {
        path: '/sign-in-out',
        icon: 'el-icon-user-solid',
        title: '签到 / 签退'
      }
    ]
    if (window.sessionStorage.getItem('admin') == 'true') {
      adminMenus.forEach((menu) => {
        menuItemsInfo.push(menu)
      })
    }
    const handleItem = function (path: string) {
      router.push({ path })
    }
    return {
      menuItemsInfo,
      handleItem
    }
  }
})
</script>

<style lang="less" scoped>
.nav-menu {
  height: 100%;
  background: var(--bg-color-1);
  .nav-icon {
    height: 48px;
    font-size: 20px;
    padding: 0 20px;
    color: var(--text-color-f);
    display: flex;
    align-items: center;
    justify-content: center;
    span {
      height: 48px;
      line-height: 48px;
      overflow: hidden;
    }
  }
  .el-menu {
    border-right: none;
  }
}
</style>
