
import { defineComponent } from 'vue'
import { useStore } from 'vuex'
import { logout } from '@/service/nav-header'

export default defineComponent({
  name: 'NavHeader',
  setup() {
    const store = useStore()
    const collapse = () => {
      store.commit('collapseNav')
    }
    return {
      collapse,
      logout
    }
  }
})
